import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { NavigationComponent } from './parts/navigation/navigation.component';
import { CardComponent } from './pages/card/card.component';
import { PaginationComponent } from './parts/pagination/pagination.component';
import { AppRoutingModule } from './app-routing.module';
import { LoginComponent } from './pages/login/login.component';
import { SignupComponent } from './pages/signup/signup.component';
import { DetailComponent } from './pages/product-detail/detail.component';
import { FormsModule } from '@angular/forms';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { CartComponent } from './pages/cart/cart.component';
import { CookieService } from "ngx-cookie-service";
import { ErrorInterceptor } from "./_interceptors/error-interceptor.service";
import { JwtInterceptor } from "./_interceptors/jwt-interceptor.service";
import { OrderComponent } from './pages/order/order.component';
import { OrderDetailComponent } from './pages/order-detail/order-detail.component';
import { ProductListComponent } from './pages/product-list/product.list.component';
import { UserDetailComponent } from './pages/user-edit/user-detail.component';
import { ProductEditComponent } from './pages/product-edit/product-edit.component';
import { ProductAddComponent } from './pages/product-add/product-add.component';
import { CategoryAddComponent } from './pages/category-add/category-add.component';
import { CategoryEditComponent } from './pages/category-edit/category-edit.component';
import { CategoryListComponent } from './pages/category-list/category.list.component';
import { StatsComponent } from './pages/statistics/stats.component';
import { UserListComponent } from './pages/user-list/user.list.component';
import { LoadingInterceptor } from './_interceptors/loaderInterceptor.service';
import { LoadingService } from './services/loadingService';
import { SpinnerComponent } from './pages/spinner/spinner-component';

@NgModule({
    declarations: [
        AppComponent,
        NavigationComponent,
        CardComponent,
        PaginationComponent,
        LoginComponent,
        SignupComponent,
        DetailComponent,
        CartComponent,
        OrderComponent,
        OrderDetailComponent,
        ProductListComponent,
        UserDetailComponent,
        ProductEditComponent,
        ProductAddComponent,
        CategoryAddComponent,
        CategoryEditComponent,
        CategoryListComponent,
        StatsComponent,
        UserListComponent,
        SpinnerComponent
    ],
    imports: [
        BrowserModule,
        AppRoutingModule,
        FormsModule,
        HttpClientModule,

    ],
    providers: [CookieService, LoadingService,
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: LoadingInterceptor, multi: true }],
    bootstrap: [AppComponent]
})
export class AppModule {
}
